import React, { FC } from "react"
import { analyticsTrack } from "../utils"
import { StoreIcon } from "../assets/icons"

const styles = {
  storeButton: {
    minHeight: "64px",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    textDecoration: "none",
    gap: "16px",
  },
  storeIcon: {
    width: "30px",
    marginRight: "20px",
  },

  otherParticipant: {
    height: "556px",
  },
}
interface ParticipantProps {
  participant: {
    name: string
    option1: string
    option2: string
    option3: string
    comments: string
  }
  isSecretFriend?: boolean
}

const Participant: FC<ParticipantProps> = ({
  participant,
  isSecretFriend = false,
}) => {
  return (
    <div className="participantForm">
      <form>
        <div
          className="participantCard"
          style={isSecretFriend ? {} : styles.otherParticipant}
        >
          <div className="cardTexts">
            <h4 className="participantCardTitle">
              {isSecretFriend
                ? "O que meu amigo quer ganhar"
                : participant?.name}
            </h4>
            <h5 className="participantCardOptions ">Opes de presente</h5>
          </div>
          <div className="form-group">
            <textarea
              className="participantOption"
              placeholder="Opo 1"
              rows={1}
              readOnly={true}
              defaultValue={participant?.option1 || ""}
            ></textarea>
          </div>
          <div className="form-group">
            <textarea
              className="participantOption"
              placeholder="Opo 2"
              rows={1}
              readOnly={true}
              defaultValue={participant?.option2 || ""}
            ></textarea>
          </div>
          <div className="form-group">
            <textarea
              className="participantOption"
              placeholder="Opo 3"
              rows={1}
              readOnly={true}
              defaultValue={participant?.option3 || ""}
            ></textarea>
          </div>
          <h6 className="mb-3">Observaes</h6>
          <div className={`form-group ${isSecretFriend && "mb-3"}`}>
            <textarea
              className="participantOption"
              placeholder="(comentrios)"
              style={{ height: "100px" }}
              readOnly={true}
              rows={5}
              defaultValue={participant?.comments || ""}
            ></textarea>
          </div>

          <div className="shopButtonContainer">
            {isSecretFriend && (
              <a
                href="https://loja.papelzinho.com/"
                onClick={() =>
                  analyticsTrack("store-button", "clique", "Loja Papelzinho")
                }
                className="papelzinhoShopButton"
                style={styles.storeButton}
                target="_blank"
                rel="noreferrer"
              >
                Loja Papelzinho
              </a>
            )}
          </div>
        </div>
      </form>
    </div>
  )
}

export { Participant }
