import * as React from "react"

const ParticipantForm = ({ participant }) => {
  const [option1, setOption1] = React.useState(participant.option1)
  const [option2, setOption2] = React.useState(participant.option2)
  const [option3, setOption3] = React.useState(participant.option3)
  const [comment, setComment] = React.useState(participant.comment)
  const [loading, setLoading] = React.useState(false)

  const handleSubmit = (event) => {
    event.preventDefault()

    if (loading) {
      return
    }
    setLoading(true)

    const data = {
      groupId: participant.groupId,
      participantId: participant.id,
      option1: option1,
      option2: option2,
      option3: option3,
      comments: comment,
    }

    fetch(`${process.env.GATSBY_API_HOST}/participant/options/`, {
      method: "PATCH",
      body: JSON.stringify(data),
    })
      .then((response) => response.text())
      .then(() => {
        setLoading(false)
        alert("Informaes salvas com sucesso!")
      })
      .catch(() => {
        setLoading(false)
        alert("Algo deu errado. Tente novamente.")
      })
  }

  return (
    <div className="participantForm">
      <form className="h-100 shadow" onSubmit={handleSubmit}>
        <div className="participantCard">
          
          <div className="cardTexts">
            <h3 className="participantCardTitle">Minha lista</h3>
            <h6 className="participantCardOptions">Opes de presente</h6>
          </div>
        
          <div className="form-group">
            <textarea
              className="participantFormInput"
              placeholder="Opo 1"
              onInput={(e) => setOption1(e.target.value)}
              rows={1}
              defaultValue={participant?.option1 ? participant.option1 : ""}
            ></textarea>
          </div>
          <div className="form-group">
            <textarea
              className="participantFormInput"
              placeholder="Opo 2"
              rows={1}
              onInput={(e) => setOption2(e.target.value)}
              defaultValue={participant?.option2 ? participant.option2 : ""}
            ></textarea>
          </div>
          <div className="form-group">
            <textarea
              className="participantFormInput"
              placeholder="Opo 3"
              rows={1}
              onInput={(e) => setOption3(e.target.value)}
              defaultValue={participant?.option3 ? participant.option3 : ""}
            ></textarea>
          </div>
          <h6 className="mb-3">Observaes</h6>
          <div className="form-group">
            <textarea
              className="participantFormInput"
              placeholder="(comentrios)"
              style={{ height: "100px" }}
              rows={5}
              onInput={(e) => setComment(e.target.value)}
              defaultValue={participant?.comments ? participant.comments : ""}
            ></textarea>
          </div>
          <div className="shopButtonContainer">
            <button
              type="submit"
              className="papelzinhoShopButton"
              style={{ minHeight: "64px" }}
            >
              {loading ? (
                <span>
                  <span
                    className="spinner-border spinner-border-sm mr-1"
                    role="status"
                    aria-hidden="true"
                  ></span>
                  Salvando...
                </span>
              ) : (
                "Salvar"
              )}
            </button>
          </div>
        </div>
      </form>
    </div>
  )
}

export { ParticipantForm }
